@custom-media --xs (min-width: 480px);
@custom-media --xs-down (max-width: 479px);
@custom-media --sm (min-width: 640px);
@custom-media --sm-down (max-width: 639px);
@custom-media --md (min-width: 768px);
@custom-media --md-down (max-width: 767px);
@custom-media --lg (min-width: 1024px);
@custom-media --lg-down (max-width: 1023px);
@custom-media --xl (min-width: 1280px);
@custom-media --xl-down (max-width: 1279px);
@custom-media --2-xl (min-width: 1536px);
@custom-media --2-xl-down (max-width: 1535px);
.wp-block-column {
  flex-basis: 0;
  flex-grow: 1;
}
.wp-block-column:where(.is-vertically-aligned-top) {
  align-self: flex-start;
}
.wp-block-column:where(.is-vertically-aligned-center) {
  align-self: center;
}
.wp-block-column:where(.is-vertically-aligned-bottom) {
  align-self: flex-end;
}